    <script>
    import HeaderTop from '../components/HeaderTop.vue';
         export default {
            components:{HeaderTop},
            data() {
                return {
                    has_read_messages:[],
                    hasnot_read_messages:[],
                }
            },
            async created () {
                const res = await this.$http.get(`/messages?accesstoken=${sessionStorage.getItem('token')}`)
                this.has_read_messages = res.data.has_read_messages
                this.hasnot_read_messages =res. data.hasnot_read_messages
            },
            methods:{
              async  allRead(){
               await this.$http({
                        method:'POST',
                        url:'/message/mark_all',
                        data:{
                            accesstoken:sessionStorage.getItem('token')
                        }
                    })
                    this.$store.commit('clearCount')
                  let newarr =  this.hasnot_read_messages=[]
                  this.hasnot_read_messages=[]
                this.has_read_messages=this.has_read_message.concat(newarr)

                },
                async readed(id){
                    await this.$http({
                        method:'post',
                        url:`/message/mark_one/${id}`,
                        data:{
                            accesstoken:sessionStorage.getItem('token')
                        }
                    })
                    this.$store.commit('reduceCount')
                    this.hasnot_read_messages=this.hasnot_read_messages.filter((ele) => {
                        return ele.id != id
                    })
                    let newarr = this.hasnot_read_messages.find((ele) => {
                        return ele.id === id
                    })
                    this.has_read_messages.push(newarr)
                }
            }
        }
    </script>
     
